.book-result {
  border: 1px solid black;
  padding: 2rem;
  margin-bottom: 1rem;
  position: relative;
  min-height: 22rem;
}

.title {
  width: calc(100% - 50px);
}

.author {
  font-weight: bolder;
}

.description {
  margin-bottom: 0;
}

.image {
  height: 150px;
  width: auto;
  float: left;
  margin-right: 1rem;
  margin-top: 0.3rem;
}

.btn-panel {
  position: absolute;
  top: 2rem;
  right: 2rem;
}

.btn-primary {
  display: block;
  line-height: 1.0;
}

.save-btn {
  margin-top: 0.5rem;
}

@media (max-width: 576px) {
  .title {
    font-size: 24px;
  }

  .description {
    font-size: 14px;
  }
}