body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: black;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.jumbotron {
  min-height: 300px;
  padding-top: 100px;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(7, 8, 8, 0.6)), to(rgba(8, 8, 8, 0.6))), 
    url(/static/media/bookcase-books-bookstore-775998.d8631c5e.jpg);
  background: linear-gradient(to bottom, rgba(7, 8, 8, 0.6) 0%, rgba(8, 8, 8, 0.6) 100%), 
    url(/static/media/bookcase-books-bookstore-775998.d8631c5e.jpg);
  background-position: center;
  background-size: cover;
  color: white;
}

.jumbotron a {
  color: white;
}

h1, h2 {
  font-family: 'Alice', serif;
}

p, a {
  font-family: 'Montserrat', sans-serif;
}

.book-search {
    margin-bottom: 2rem;
    font-family: 'Montserrat', sans-serif;
}

.btn {
    border-color: hsl(180, 40%, 2%);
    background: -webkit-gradient(linear,left top, left bottom,from(#000000), to(#000000) );
    background: linear-gradient(to bottom,#000000, #000000 );
}

.btn:hover {
    background: -webkit-gradient(linear,  left top, left bottom,  color-stop(33%, #000000),  to(#000000) );
    background: linear-gradient(to bottom,  #000000 33%,  #000000 );
}

input, label, .btn {
    font-family: 'Montserrat', sans-serif;
}
.book-result {
  border: 1px solid black;
  padding: 2rem;
  margin-bottom: 1rem;
  position: relative;
  min-height: 22rem;
}

.title {
  width: calc(100% - 50px);
}

.author {
  font-weight: bolder;
}

.description {
  margin-bottom: 0;
}

.image {
  height: 150px;
  width: auto;
  float: left;
  margin-right: 1rem;
  margin-top: 0.3rem;
}

.btn-panel {
  position: absolute;
  top: 2rem;
  right: 2rem;
}

.btn-primary {
  display: block;
  line-height: 1.0;
}

.save-btn {
  margin-top: 0.5rem;
}

@media (max-width: 576px) {
  .title {
    font-size: 24px;
  }

  .description {
    font-size: 14px;
  }
}
nav.navbar {
  margin-bottom: 0;
}

.navbar-brand {
  font-family: 'Alice', serif;
}
